/* eslint-disable no-async-promise-executor */
import axios from "@/api/axios";
import store from "@/store";

import UserService from "@/services/UserService";

const authService = {
  getPageAccessToken(id) {
    return new Promise(async (resolve, reject) => {
      let params = {};
      params["access_token"] = this.getToken()["token"];
      try {
        let { data } = await axios({
          method: "GET",
          url: `${id}/accounts`,
          params,
        });
        sessionStorage.setItem("pageAccessToken", data.data[0].access_token);
        resolve(data);
      } catch (e) {
        reject(e);
        throw e;
      }
    });
  },
  getToken() {
    return {
      token: sessionStorage.getItem("accessToken"),
    };
  },
  storeSignin() {
    store.dispatch("auth/signin", this.getToken());
  },
  setSignin(data) {
    sessionStorage.setItem("accessToken", data.accessToken);
    store.dispatch("auth/signin", data);
  },
  setSignout() {
    sessionStorage.removeItem("accessToken");
    store.dispatch("auth/signout");
  },
  async init() {
    this.storeSignin();
    if (store.getters["auth/isSignin"] === false) {
      return;
    }
    await UserService.getMe();
  },
};

export default authService;
