<script>
// @ is an alias to /src
import Header from "../components/Header.vue";
import AuthService from "../services/AuthService";
export default {
  name: "Home",
  components: {
    Header,
  },
  data: () => ({
    i: 0,
    animationTime: 0,
    animation: 0,
    instagramBusinessAccount: null,
    profile: null,
    insights: [],
    isInsightsLoading: false,
    postTrendData: null,
    postStatisticData: null,
  }),
  async created() {
    this.profile = JSON.parse(sessionStorage.getItem("profile"));
  },
  computed: {
    getPageToken() {
      return sessionStorage.getItem("accessToken");
    },
  },
  methods: {
    async auth() {
      try {
        await this.getPageAccessToken();
        await this.getInstagramBusinessAccount();
      } catch (e) {
        alert(e);
        this.$router.push({ name: "Login" });
      }
    },
    async getPageAccessToken() {
      await AuthService.getPageAccessToken(this.profile.id);
    },
    async getInstagramBusinessAccount() {
      let $this = this;
      window.FB.api(
        "/me",
        "GET",
        {
          access_token: sessionStorage.getItem("pageAccessToken"),
          fields: "instagram_business_account",
        },
        function (response) {
          $this.instagramBusinessAccount = response.instagram_business_account;
        }
      );
    },
    async getPostTrendData() {
      let $this = this;
      window.FB.api(
        `/${this.instagramBusinessAccount.id}`,
        "GET",
        {
          access_token: sessionStorage.getItem("pageAccessToken"),
          fields:
            "business_discovery.username(ama_lee91){id,name,username,profile_picture_url,biography,website,followers_count,follows_count,media_count,media.limit(100000){caption,media_type,media_url,timestamp,permalink,comments_count,like_count}}",
          period: "lifetime",
        },
        function (response) {
          console.log(response.business_discovery);
          $this.postTrendData = response.business_discovery;

          let medias = $this.postTrendData.media.data;
          let mediasCount = medias.length;

          let totalLikes = medias.reduce(
            (accumulator, e) => accumulator + e.like_count,
            0
          );
          let totalComments = medias.reduce(
            (accumulator, e) => accumulator + e.comments_count,
            0
          );

          $this.postTrendData.averagePostLikes =
            totalLikes > 0 ? Math.floor(totalLikes / mediasCount) : 0;
          $this.postTrendData.averagePostComments =
            totalComments > 0 ? Math.floor(totalComments / mediasCount) : 0;
        }
      );
    },
    async getDailyTrendData() {
      let $this = this;
      window.FB.api(
        `/${this.instagramBusinessAccount.id}/insights`,
        "GET",
        {
          access_token: sessionStorage.getItem("pageAccessToken"),
          metric:
            "get_directions_clicks,website_clicks,profile_views,email_contacts,impressions,reach,profile_views,follower_count",
          period: "day",
        },
        function (response) {
          console.log("getDailyTrendData", response);
          $this.insights = [...response.data];
        }
      );
    },
    async getLifeTimeTrendData() {
      // let $this = this;
      window.FB.api(
        `/${this.instagramBusinessAccount.id}/insights`,
        "GET",
        {
          access_token: sessionStorage.getItem("pageAccessToken"),
          metric: "audience_country,audience_city,audience_gender_age",
          period: "lifetime",
        },
        function (response) {
          console.log("getLifeTimeTrendData", response);
          // $this.insights = [...response.data];
        }
      );
    },
    async getData() {
      await this.getPostTrendData();
      await this.getDailyTrendData();
      await this.getLifeTimeTrendData();
    },
    // getPostData() {
    //   window.FB.api(
    //     `/${this.profile.id}/feed`,
    //     "GET",
