import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

const auth = {
  name: "auth",
  namespaced: true,
  state: {
    token: null,
    refreshToken: null,
  },
  getters: {
    isSignin: (state) => !!state.token,
  },
  mutations: {
    signin(state, data) {
      state.token = data.token;
      state.refreshToken = data.refreshToken;
    },
    signout(state) {
      for (const k in state) {
        state[k] = null;
      }
    },
  },
  actions: {
    signin({ commit }, data) {
      commit("signin", data);
    },
    signout({ commit }) {
      commit("signout");
    },
  },
};

export default auth;
